body {
  background-color: #eee;
  color: #333;
  box-sizing: border-box;
}

img {
  vertical-align: middle;
}

::placeholder {
  color: gray;
}

.btn {
  border-radius: 4px;
  box-shadow: 0 2px 4px rgba(51, 51, 51, 0.3);
}

.image {
  background-image: url(image@1x.png);
}

@media (min-resolution: 2dppx) {
  .image {
    background-image: url(image@2x.png);
  }
}